import { defineComponent } from "libraries/ui/define-component";
import { div } from "libraries/ui/html-elements";

interface IHeaderProps {
  class: string;
}

class HeaderComponent extends HTMLElement {
  private props?: IHeaderProps;

  private connectedCallback(): void {
    this.render();
  }

  private template(): HTMLElement {
    return div();
  }

  private render(): void {
    if (this.props === undefined) {
      throw new Error("Props is not defined");
    }

    this.replaceChildren(this.template());
  }

  public init(props: IHeaderProps) {
    this.props = props;
    this.classList.add(props.class);
  }
}

export const Header = defineComponent<HeaderComponent, IHeaderProps>("app-header", HeaderComponent);
