import { defineComponent } from "libraries/ui/define-component";
import { div } from "libraries/ui/html-elements";

interface IFeatureProps {}

class FeatureComponent extends HTMLElement {
  constructor() {
    super();
    this.attachShadow({
      mode: "open",
    });
  }

  static get observedAttributes() {
    return [];
  }

  private props: IFeatureProps | null = null;

  private connectedCallback(): void {
    this.render();
  }

  private template(): HTMLElement[] {
    return [
      div("string 1"), //
      div("string 2"),
    ];
  }

  private render(): void {
    const child = this.template();

    this.shadowRoot?.replaceChildren(...child);
  }

  public init(props?: IFeatureProps) {
    if (props === undefined) {
      return;
    }

    this.props = props;
  }
}

export const Feature = defineComponent<FeatureComponent, IFeatureProps>("feature-component", FeatureComponent);
