import { App } from "app/components/app.component";
import { startPwaServiceWorker } from "app/services/pwa/start-pwa-service-worker";
import "app/styles/global.css";
import { render } from "libraries/ui/render";
import { PermanentScreenSlider } from "libraries/screen-slider";
import { ScreenTransition } from "libraries/screen-transition";

startPwaServiceWorker();

const root = document.getElementById("root");

if (root === null) {
  throw new Error("No root found.");
}

root.append(
  render(
    new App({
      contentScreenSlider: new PermanentScreenSlider({
        allowedArea: 50,
        threshold: 0.3,
        animationDuration: 300,
      }),
      appScreenTransition: new ScreenTransition(),
    })
  )
);
