import { Feature } from "features/some-feature-module/components/feature.component";
import { defineComponent } from "libraries/ui/define-component";
import { button, div } from "libraries/ui/html-elements";
import * as styles from "app/components/screens/screen-1/screen-1.module.css";

interface IScreen1Props {
  greeting: string;
  getRandomNumber: () => string;
  class?: string;
  onNextScreenClick: () => void;
}

class Screen1Component extends HTMLElement {
  private props!: IScreen1Props;

  private connectedCallback(): void {
    this.render();
  }

  private template(): HTMLElement {
    return div(
      button("Next screen", {
        onclick: this.props.onNextScreenClick,
      }),
      div(this.props.greeting), //
      div(this.props.getRandomNumber()),
      new Feature(),
      div({
        style: "height: 5rem; background-color: gray",
      }),
      div({
        style: "height: 5rem; background-color: white",
      }),
      div({
        style: "height: 5rem; background-color: gray",
      }),
      div({
        style: "height: 5rem; background-color: white",
      }),
      div({
        style: "height: 5rem; background-color: gray",
      }),
      div({
        style: "height: 5rem; background-color: white",
      }),
      div({
        style: "height: 5rem; background-color: gray",
      }),
      div({
        style: "height: 5rem; background-color: white",
      }),
      div({
        style: "height: 5rem; background-color: gray",
      }),
      div({
        style: "height: 5rem; background-color: white",
      }),
      div({
        style: "height: 5rem; background-color: gray",
      }),
      div({
        style: "height: 5rem; background-color: white",
      }),
      div({
        style: "height: 5rem; background-color: gray",
      }),
      div({
        style: "height: 5rem; background-color: white",
      }),
      div({
        style: "height: 5rem; background-color: gray",
      }),
      {
        class: styles.screen,
      }
    );
  }

  private render(): void {
    this.replaceChildren(this.template());
  }

  public init(props: IScreen1Props) {
    this.id = "screen-1";
    this.props = props;
    if (props.class !== undefined) {
      this.classList.add(props.class);
    }
  }
}

export const Screen1 = defineComponent<Screen1Component, IScreen1Props>("screen-1", Screen1Component);
