import { isDevelopment } from "app/services/is-development";

export async function startPwaServiceWorker(): Promise<void> {
  if (isDevelopment()) {
    return;
  }

  if ("serviceWorker" in navigator) {
    const { Workbox } = await import("workbox-window");

    const wb = new Workbox("./service-worker.js");
    wb.register();
  }
}
