import { Screen1 } from "app/components/screens/screen-1/screen-1.component";
import { div } from "libraries/ui/html-elements";
import { defineComponent } from "libraries/ui/define-component";
import * as styles from "app/components/content/content.module.css";
import { Screen2 } from "app/components/screens/screen-2/screen-2.component";
import { render } from "libraries/ui/render";
import { Screen3 } from "app/components/screens/screen-3/screen-3.component";
import { PermanentScreenSlider } from "libraries/screen-slider";

interface IContentProps {
  greeting: string;
  getRandomNumber: () => string;
  class: string;
  onNextScreenClick: () => void;
  screenSlider: PermanentScreenSlider;
}

class ContentComponent extends HTMLElement {
  private props!: IContentProps;

  private connectedCallback(): void {
    const props = this.props;

    this.render();

    const contentScreens = render<HTMLElement[]>([
      new Screen1({
        getRandomNumber: props.getRandomNumber,
        greeting: props.greeting,
        onNextScreenClick: props.onNextScreenClick,
      }),
      new Screen2({
        screenSlider: this.props.screenSlider,
      }),
      new Screen3(),
    ]);
    this.props?.screenSlider.init("#content-screen-container", contentScreens);
  }

  private template(): HTMLElement {
    return div({
      id: "content-screen-container",
      class: styles.screenContainer,
    });
  }

  private render(): void {
    this.replaceChildren(this.template());
  }

  public init(props: IContentProps) {
    this.props = props;
    this.classList.add(props.class);
  }
}

export const Content = defineComponent<ContentComponent, IContentProps>("app-content", ContentComponent);
