import { Component } from "./component";

export function defineComponent<T extends HTMLElement & { init(props?: P): void }, P = {}>(
  tagName: string, //
  ctr: CustomElementConstructor //
) {
  customElements.define(tagName, ctr);

  return class implements Component {
    private readonly htmlElement: T;

    constructor(props?: P) {
      this.htmlElement = document.createElement(tagName) as T;

      this.htmlElement.init(props);
    }

    render(): HTMLElement {
      return this.htmlElement;
    }

    isComponent(): boolean {
      return true;
    }
  };
}
